import React from 'react'
import DisplayCategorie from '../display/DisplayCategorie'

class Semester extends React.Component {
    componentDidMount() {
        document.title = `Charlinfo | ${this.props.title}`;
    }
    render() {
        const informatique = this.props.dataClass.filter(data => data.type === "informatique");
        const general = this.props.dataClass.filter(data => data.type === "general");
        return (
            <div className="container mt-3">
                <h1 className="text-center mb-3">{this.props.title}</h1>
                <div className="s1_container">
                    <div className="s1_col">
                        <h3 className="text-center mb-3">UE Informatique</h3>
                        {informatique.map((data) => (
                            <DisplayCategorie data={data} link={`/${this.props.title}/${data.link}`} key={data.id} />
                        ))}
                    </div>
                    <div className="s1_col">
                    <h3 className="text-center mb-3">UE Générale</h3>
                        {general.map((data) => (
                            <DisplayCategorie data={data} link={`/${this.props.title}/${data.link}`} key={data.id} />
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}

export default Semester